import React, { useState } from "react";
//import {useNavigate } from "react-router-dom";
import { useMediaQuery } from 'react-responsive'
import Sidebar from "components/Sidebar";
import MobileNavbar from "components/MobileNavbar";
import Content from "routes/Main";
function App(){
  const isDesktopOrLaptop = useMediaQuery({
    query: '(min-width: 1224px)'
  })
  const [collapsed, setCollapsed] = useState(true);
  const [sidebarFija, setSidebarFija] = useState(false);
  //const navigate = useNavigate();
  function mouseEnterHandler (){
    if(!sidebarFija && isDesktopOrLaptop){ 
      setCollapsed(true)
    }
  }
  function mouseLeaveHandle (){
    if(!sidebarFija && isDesktopOrLaptop){ 
      setCollapsed(true)
    }
  }
  return (
    <div className={"wrapper-body " + (isDesktopOrLaptop?'':'mobile')} onMouseLeave={mouseLeaveHandle}>
      {
        isDesktopOrLaptop?
        (
        <Sidebar collapsed={collapsed} setCollapsed={setCollapsed} sidebarFija={sidebarFija} setSidebarFija={setSidebarFija}/>
        )
        :
        (
          <MobileNavbar collapsed={collapsed}/>
        )
      }
      <div className={"body-container " + (isDesktopOrLaptop?'':'mobile')} onMouseEnter={mouseEnterHandler}>
        <div className="container">
          <Content/>
        </div>
      </div>
    </div>
      
 
  )
 
}
export default App;