import React from "react";
import { Link } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import Button from "components/globals/Button";
import { useMediaQuery } from 'react-responsive'

function Toolbar(props) {
  const { section } = props;
  const isLarge = useMediaQuery({
    query: '(min-width: 1080px)'
  })
  const isMobile = useMediaQuery({
    query: '(max-width: 800px)'
  })
  //chales no se hacer esto 
  const sections = {
    contact: 'contact',
    resume: 'resume'
  };
  const currentSection = sections[section] ?? 'about';

  function handleMail() {
    window.location = 'mailto:tuemail@dominio.com'
  }
  return (
    <div className={"wrapper-toolbar " + (isMobile ? 'mobile' : '')}>
      <Row className={"toolbar float-box shadow mb-4 " + (isMobile ? 'mobile' : '')}>
        <Col xs="auto">
          <nav>
            <Link to="" className={"link-button " + (currentSection === "about" ? 'active' : '')} style={{ marginLeft: 0 }}>Principal</Link>
            <Link to="resume" className={"link-button " + (currentSection === "resume" ? 'active' : '')}> Experiencia</Link>
            <Link to="contact" className={"link-button " + (currentSection === "contact" ? 'active' : '')}> Contacto</Link>
          </nav>
        </Col>
        <Col></Col>
        {
          isLarge && (
            <Col xs="auto">
              <a className={"link-button "} href="https://www.facebook.com/hugorafael.iceloflores" target="_blank" rel="noreferrer"><i className="fa-brands fa-facebook"></i></a>
              <a className={"link-button "} href="https://github.com/HugoRIF" target="_blank" rel="noreferrer"><i className="fa-brands fa-github"></i></a>
              <a className={"link-button "} href="https://www.linkedin.com/in/hugo-rafael-icelo-flores-aa2709b5" target="_blank" rel="noreferrer"><i className="fa-brands fa-linkedin-in"></i></a>
              <Button flat width="135px" onClick={handleMail} icon="fa-solid fa-paper-plane">
                Contactame
              </Button>
            </Col>

          )
        }



      </Row>
    </div>


  );
}
export default Toolbar;