import React from "react";
import { Link } from "react-router-dom";
import { ProSidebar, SidebarHeader, SidebarFooter, SidebarContent, Menu, MenuItem } from 'react-pro-sidebar';
import './styles.scss';

const Icon = (props) => {
  return (
    <span className={props.name}> </span>
  );
};
function Main(props) {
  const {
    collapsed,
    setCollapsed,
    //sidebarFija,
    //setSidebarFija
  } = props;
  function handleEnter(){
    setCollapsed(false);
  }
  return (

    <ProSidebar collapsed={collapsed}
      collapsedWidth={70} width={200} style={{ position: 'fixed', height: '100vh', top: 0 }}
    >
      <SidebarHeader onMouseEnter={handleEnter} >
        <Menu>
          <MenuItem
            icon={collapsed ? <Icon name="fa fa-bars" /> : (<><Icon name="fa fa-chevron-left" /><Icon name="fa fa-chevron-left" /></>)}
            onClick={() => { setCollapsed(!collapsed) }}
          />

        </Menu>
      </SidebarHeader>
      <SidebarContent onMouseEnter={handleEnter}>
        <Menu >
          <MenuItem icon={<Icon name="fa fa-home" />}>
            <Link to="/">Home</Link>
          </MenuItem>
          <MenuItem icon={<Icon name="fa fa-folder-open" />}>
            <Link to="/portafolio">Portafolio</Link>
          </MenuItem>
        </Menu>
      </SidebarContent>
      <SidebarFooter onMouseEnter={handleEnter}>
        {/**
     *  You can add a footer for the sidebar ex: copyright
     */}
      </SidebarFooter>
    </ProSidebar>


  );

}
export default Main;