import React from "react";

import styled from "styled-components";
import ClipLoader from "react-spinners/ClipLoader";
import colors from "colors.scss";
const StyledButton = styled.button`
  font-size: 0.9em;
  font-weight: bold;
  line-height: 1em;
  width: ${props => props.width? props.width:'max-content'};
  padding: 8px;
  cursor:pointer;
  margin:${props => props.margin? props.margin:'auto'};
  ${
    (props)=>{
      if(props.disabled){
        return `
        color: #D8D8D8 !important;
        cursor:default !important;
        background-color: transparent !important;
        border: 1px #D8D8D8 solid;
        border-radius: 40px;
        &:hover{
          background-color: #fff;
        } 
      `;
      }
  
      if(props.flat){
        return `
          color: #fff;
          background-color: ${props.loading?colors.default_dark:colors.default};
          border: none;
          border-radius: 40px;
          &:hover{
            opaciy: 0.7;
            background-color: ${colors.default_dark};
           
          } 
         
        `;
      }
      else{
        return `
          color: ${props.loading?'white':colors.default};
          background-color:${props.loading?colors.default_dark:'#fff'};
          border: ${props.border? '1px solid '+colors.default:'none'};
          border-radius: 40px;
          &:hover{
            background-color: ${props.loading?colors.default_dark:colors.default};
            color: white;
            span{
              border-color:white;
              border-bottom-color:transparent !important;
            }
          } 
        `;
      }
      
    }
    
  }
 
`;
function Button(props){
  function handleClick(e){
    e.preventDefault();
    if(props.loading === false || props.loading === undefined){
      props.onClick(e)
    }
  }
  return(
    <StyledButton 
      id={props.id}
      onClick = {handleClick} 
      width={props.width}
      border={props.border}
      flat={props.flat}
      margin={props.margin}
      disabled={props.disabled}
      style={props.style}
      loading={props.loading?1:0}
      className={'boton '}
      >
        {
          props.loading?(
            <>
              <ClipLoader color={'white'} speedMultiplier={1}  size={'0.9em'}/>
            </>
          )
          :
          (
            <>
            {props.children}
            <span className={props.icon  + " ms-2"}/>
            </>
          )
        }
    </StyledButton>
  );
}
Button.defaultProps = {
  loading: false
}
export default Button;