import React from "react";
import styled from "styled-components";
import "./style.scss";
import colors from "colors.scss";

const Ul = styled.ul`
  li{
    &::before{
      margin-top:  ${props => props.bigSize? "8px":"2px"};

    }
  }
  li:first-child{
    padding-bottom:  ${props => props.bigSize? "30px":"10px"};
    &::before{
      content: '${props => props.icon? props.icon:"\f0e7"}';
      font-size: ${props => props.bigSize? '18px':"14px"};
      margin: ${props => props.bigSize? "-40px 0 0 -58px":"-40px 0 0 -51px"};
      padding: ${props => props.bigSize? "14px 18px":"11px 14px"};
      width: ${props => props.bigSize? "53px":"40px"};
      height: ${props => props.bigSize? "53px":"40px"};

    }
    h4{
      color: ${colors.font_black} !important;
      position: absolute;
      top: -1em;
      margin-left: 0.5em;
      font-weight:600;
      font-size:1.6em;
    }
  }
`;

function CustomList(props){
  const {
    title,
    icon,
    bigSize
  } = props;
  return(
    <Ul 
      icon={icon}
      bigSize={bigSize?1:0}
      className="custom_list"
    >
      <li>
        {
          title && (
            <h4>{title}</h4>

          )
        }
      </li>
      {
        props.content.map((item,index)=>{
          return (
          <li key={"item_"+index}>
            {
              item && (
                <>{item}</>
              )
            }
          </li>
          )
        })
      }
    </Ul>
  );
}
export default CustomList;