import React,{ useRef} from "react";
import "./style.scss";
import photo from "./photo.png";
import Info from "./Info";
import Button from "components/globals/Button";
import { Row } from "react-bootstrap";
import { useMediaQuery } from 'react-responsive'
import pdf from "./CV_HugoIcelo.pdf";
function Main(props){
  const isMobile = useMediaQuery({
    query: '(max-width: 800px)'
  })
  const linkPDF = useRef(null);
  const Profile = () => (
    <div className="profile">
      <span className="name">Hugo Icelo</span> <br/>
      <span className="charge">Full Stack Developer</span>
    </div>
  );
  const Photo = () => (
    <figure className="profile-photo">
      <img src={photo} alt="profile_photo"></img>
    </figure>
  );

  function handleDownloadCV(){
    console.log("se descarga el cv");
    if(linkPDF){
      linkPDF.current.click();
    }
  }

    return (
    <div className={"main_info-container shadow "+(isMobile?'mobile':'')}>
      <div className="profile-container pt-3">
        <Photo/>
        <Profile/>
      </div>
      
      <Info/>
      <Row>

        <Button onClick={handleDownloadCV}margin={'10px auto 20px'} width={'200px'}  flat icon={'fa fa-download'}>
            Descargar CV  
        </Button>
      <a ref={linkPDF} href={pdf} target="_blank" rel="noopener noreferrer" download="CV_HugoIcelo.pdf" hidden>download</a> 

      </Row>
    </div>
    
  );
}
export default Main;